#topW,
#topN,
#topE,
#midW,
#midE,
#botW,
#botS,
#botE,
#topLineSquare {
  position: absolute;
  left: -10px;
  top: -10px;
  width: 8px;
  height: 8px;
  border: solid 1px black;
  z-index: 9000;
  background-color: #473939;
}
#frame {
  position: absolute;
  left: -10px;
  top: -10px;
  width: 8px;
  height: 8px;
  border: solid 1px black;
}

#topW {
  cursor: nw-resize;
}
#topN {
  cursor: n-resize;
}
#topE {
  cursor: ne-resize;
}
#midW {
  cursor: w-resize;
}
#midE {
  cursor: e-resize;
}
#botW {
  cursor: sw-resize;
}
#botS {
  cursor: s-resize;
}
#botE {
  cursor: se-resize;
}
#topLine {
  position: absolute;
  height: 20px;
  border-left: 1px solid #777;
  left: -5px;
  top: -5px;
}
